import React from "react";

// components imports
import LoginForm from "../../../../Components/Login/Desktop/Components/LoginForm/LoginForm";

import Header from "../../components/Header";

// assets imports
import characterImg from "./assets/character.svg";

// // styles
// import "./styles/index.css";
// styles
import useStyles from "./styles/muiStyles";

const LoginComponent = () => {
  const classes = useStyles();

  return (
    <>
      {/* Header */}
      <Header type="focus" />

      {/* Section with text and LoginForm */}
      <section className={classes["login-body"]}>
        <div className="img-block">
          <img src={characterImg} alt="character" />
        </div>
        <LoginForm />
      </section>
    </>
  );
};

export default LoginComponent;
