import { makeStyles } from "@material-ui/core";

// menu classes
const useStyles = makeStyles((theme) => ({
  "login-body": {
    maxWidth: "95%",
    margin: "50px auto",
  },
  "@media (min-width: 960px)": {
    "login-body": {
      alignItems: "center",
      display: "flex",
      height: "100vh",
      justifyContent: "space-evenly",
      margin: "0 auto",
      width: "90%",
    },
  },
  "@media (min-width: 1280px)": {
    "login-body": {
      width: "80%",
    },
  },
  "@media (min-width: 1300px)": {
    "login-body": {
      maxWidth: "1300px",
    },
  },
}));

export default useStyles;
